ntdown */.countdown-custom .countdown:before {	content: '';	display: inline-block;	height: 100%;	vertical-align: middle;}.countdown-custom .countdown-section {	display: inline-block;	width: 60px;	height: 60px;	padding-top: 10px;	text-align: center;	border-radius: 50%;}.countdown-custom .countdown-section:nth-child(5) {	display: none;}@media (min-width: 1200px) {	.countdown-custom .countdown-section:nth-child(5) {		display: inline-block;	}}.countdown-custom .countdown-row {	display: inline-block;	vertical-align: middle;}.countdown-custom .countdown .countdown-section + .countdown-section {	margin-left: 5px;}.countdown-custom .countdown-amount {	display: block;	font-weight: 900;	font-size: 22px;	line-height: 2.2;	text-transform: uppercase;	color: #434345;}.countdown-custom .countdown-period {	display: block;	font-size: 10px;	line-height: 1;	color: #9b9b9b;}@media (min-width: 480px) {	.countdown-custom .countdown-section {		width: 92px;		height: 92px;		padding-top: 25px;		border-width: 3px;	}	.countdown-custom .countdown-amount {		line-height: 1;	}	.countdown-custom .countdown .countdown-section + .countdown-section {		margin-left: 10px;	}}@media (min-width: 768px) {	.countdown-custom .countdown-section {		width: 155px;		height: 155px;		padding-top: 40px;	}	.countdown-custom .countdown .countdown-section + .countdown-section {		margin-left: 22px;	}	.countdown-custom .countdown-amount {		font-size: 45px;	}	.countdown-custom .countdown-period {		font-size: 16px;	}}@media (min-width: 992px) {	.countdown-custom .countdown .countdown-section + .countdown-section {		margin-left: 44px;	}}.countdown-custom.countdown-modern .countdown-section {	position: relative;	z-index: 1;}.countdown-custom.countdown-modern .countdown-section .countdown-period {	display: none;}.countdown-custom.countdown-modern .countdown-section .countdown-letter {	position: absolute;	top: 0;	right: 0;	left: 0;	z-index: -1;	text-align: center;	font-size: 44px;	color: rgba(67, 67, 69, 0.2);	text-transform: uppercase;}@media (min-width: 768px) {	.countdown-custom.countdown-modern .countdown-section .countdown-letter {		font-size: 72px;		top: 8px;	}}@media (min-width: 480px) {	.countdown-custom.countdown-ellipse .countdown-section {		border: 3px solid #f9f9f9;	}}.countdown-inline {	font-size: 13px;}.countdown-inline .countdown-section .countdown-amount, .countdown-inline .countdown-section .countdown-period {	margin-right: 2px;}.countdown-inline .countdown-section .countdown-amount {	font-weight: 700;	margin-left: 4px;}@media (min-width: 480px) {	.countdown-inline {		font-size: 16px;	}	.countdown-inline .countdown-amount, .countdown-inline .countdown-period {		margin-right: 4px;	}	.countdown-inline .countdown-amount {		margin-left: 7px;	}}/** 3.26 ToTop Button */.ui-to-top {	box-shadow: 0 0px 10px 0 rgba(0, 0, 0, 0.22);	position: fixed;	right: 15px;	bottom: 15px;	overflow: hidden;	z-index: 10;	-webkit-transform: translateY(100px);	transform: translateY(100px);}.ui-to-top:focus {	color: #fff;}.ui-to-top:hover {	color: #fff;	background: #ec5216;}.ui-to-top.active {	-webkit-transform: translateY(0);	transform: translateY(0);}.mobile .ui-to-top,.tablet .ui-to-top {	display: none !important;}@media (min-width: 480px) {	.ui-to-top {		right: 40px;		bottom: 40px;	}}